.sect {
    transition: ease-in-out;
    transition-duration: 300ms;
}

.sect:hover {
    transform: scale(1.02);
    transition: ease-in-out;
    transition-duration: 300ms;
    box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);

}


/* this is to be put in the new image loader*/
.images {
    width: 100%;
    height: 100%;

    border-radius: 16px;

    object-fit: cover;

    box-shadow: -1px 10px 19px -10px rgba(0,0,0,0.67);

    transition: ease-in-out;
    transition-duration: 300ms;
}

.images:hover {
    transform: scale(1.1);
    transition: ease-in-out;
    transition-duration: 300ms;
}

@media screen and (min-width: 1200px) {
    .ProfileImage {
        width: 200px;
        height: 200px;
        border-radius: 100px;
        box-shadow: 0px 5px 14px -1px rgba(0,0,0,0.3);
    }
    
    .ProgrammingSect {
        display: block;
        
        padding: 10px;
    
        margin-top: 50px;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
    
        border-style: solid;
        border-width: 1px;
        border-radius: 16px;
        border-color: lightgray;
    }
    
    .PhotographySect {
        display: block;
        
        padding: 10px;
    
        margin-top: 50px;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
    
        border-style: solid;
        border-width: 1px;
        border-radius: 16px;
        border-color: lightgray;
    }
    
    .RepoSect {
        display: grid;
    
        grid-template-columns: 46% 46%;
        gap: 8%;
    
        width: 75%;
    
        padding: 0;
        margin: auto;
    }
}

